import { motion } from 'framer-motion'


const Button = ({color, text, showForm, buttonText}) => {
  return( 
      <>
        <motion.button onClick={showForm} className="add" style={{ backgroundColor: color }} 
        initial ={{opacity: 0.5}} 
        animate ={{opacity: 1}}
        layout 
        >{buttonText}  </motion.button>
      </>
    )
}

export default Button