import Tasks from './comps/Tasks'
import { useState, useEffect } from 'react'
import Button from './comps/Button' 
import Header from './comps/Header' 
import Form from './comps/Form' 
import Footer from './comps/Footer' 
import About from './comps/About' 
import './App.scss';
import {BrowserRouter as Router, Route, Switch, Link} from 'react-router-dom'

function App() {
  
  const [tasks, setTasks] = useState([])
  useEffect(() => {
    const getFetchedTasks = async () => {
      const getTasks = await fetchTasksFromDb();
      setTasks(getTasks)
    }
     getFetchedTasks()
  }, [])
  
    //Fetch All Tasks From Database 
   const fetchTasksFromDb = async () => {
      const res = await fetch("http://localhost:5000/tasks");
      const data = await res.json();
      return data;
    } 
    
    
    //Fetch Single Task From Database 
   const fetchTaskFromDb = async (id) => {
     const res = await fetch(`http://localhost:5000/tasks/${id}`)
     const data = await res.json()
     return data
   }
   
   
   //Show Add Tasks Form
   const [showForm, setShowForm] = useState(false)
   
  const onShow = () => {
    setShowForm(!showForm)
  }
    
    //Add Tasks
    const onAdd = async (task) => {
      const res = await fetch(`http://localhost:5000/tasks`, {
        'method': 'POST', 
        'headers': {
          'Content-type': 'application/json'
        }, 
        'body': JSON.stringify(task)
      })
      const data = await res.json()
      setTasks([...tasks, data])
    }
    
    //Toggle important Tasks
   const toggleImportantTask = async (id) => {
     const taskToToggle = await fetchTaskFromDb(id)
     const updatedTask = {...taskToToggle, important: !taskToToggle.important} 
     
     const res = await fetch(`http://localhost:5000/tasks/${id}`, {
      'method': 'PUT', 
      'headers': {
        'Content-type': 'application/json' 
      },
      'body': JSON.stringify(updatedTask)
     })
     
     const data= await res.json()
     
     setTasks(tasks.map((task) => task.id === id ? {
      ...task, important: data.important
     } : task)) 
   }
   
   //Toggle Completed Task
   
   const toggleCompleted = async (id) => {
     const taskToToggle = await fetchTaskFromDb(id);
     const updatedTask = {...taskToToggle, completed: !taskToToggle.completed}
     
     const res = await fetch(`http://localhost:5000/tasks/${id}`, {
       'method': 'PUT', 
       'headers': {
         'Content-type': 'application/json'
       }, 
       'body': JSON.stringify(updatedTask)
     })
     
     const data = await res.json()
     
     setTasks(tasks.map((task) => task.id === id ? {...task, completed: data.completed} : task))
   }
   
   //Delete Task 
   const deleteTask = async (id) => {
     await fetch(`http://localhost:5000/tasks/${id}`, {
       'method': 'DELETE'
     })
     
     setTasks(tasks.filter((task) => task.id !== id)) 
   }
  
  return (
    <div className="App">
     <Router>
      <div className="header-section">
        <Header />
        <Button color={showForm ? 'red' : 'green'} buttonText={showForm ? 'Close' : 'Add'} showForm= {onShow} />
      </div>
      <Route path='/' exact render={(props) => (
        <>
          {showForm && <Form onAdd={onAdd} />} 
        </>
      ) } />
      <Route path='/' exact render={(props) => (
        <>
          {tasks.length > 0  ? <Tasks addTask={onAdd} onDelete={deleteTask} complete={toggleCompleted} important={toggleImportantTask} tasks={tasks} />  : 'No Tasks Yet. You are Free As The Air'}  
        </>
      ) } />
        <Switch>
          <Route path="/about" component={About} />
        </Switch> 
        <Footer />
          <Route path="/" exact render={(props) => (
          <>
              <Link to="/About" id="about" >About </Link>
            </>
          ) } 
          layout 
          />
     </Router>
    </div>
  );
}

export default App;
