import {useState} from 'react'
import { motion } from 'framer-motion'

const Form = ({onAdd}) => {
  
  const [text, setText] = useState('')
  const [day, setDay] = useState('')
  const [time, setTime] = useState('')
  const [important, setImportant] = useState(false)
  
  
  const onSubmit = (e) => {
    
    e.preventDefault()
    
    onAdd({text, day, time, important})
    
    setText('')
    setDay('')
    setTime('')
    setImportant(false)
  }
  
  
  return(
     <div className="form">
      <motion.form onSubmit={onSubmit} 
      initial = {{y:-100, opacity: 0}} 
      animate = {{y: 0, opacity: 1}}
      transition = {{delay: 0.2, type: "spring", stiffness: 100 }}
      layout 
      >
        <label htmlFor="task"> Task </label>
          <input type="text" required value={text} onChange={(e) => setText(e.target.value)} placeholder="Enter Task" />
        <label htmlFor="day"> Day </label>
          <input type="date" required value={day} onChange={(e) => setDay(e.target.value)} placeholder="Enter Day" />
        <label htmlFor="time"> Time </label>
          <input type="time" required value={time} onChange={(e) => setTime(e.target.value)} placeholder="Enter Time" />
        <div className="important-input">
          <label htmlFor="important"> Important </label>
            <input type="checkbox" value={important} onChange={(e) => setImportant(e.currentTarget.checked)}  />
        </div>
        <div className="submit-center">
          <button> Enter </button>
        </div>
      </motion.form>
     </div>
    )
}

export default Form