import Task from './Task'
import {motion} from 'framer-motion'
const Tasks = ({tasks, important, complete, onDelete, addTask}) => {
  return (
    <motion.div className="tasks" 
    initial ={{y: 100, opacity: 0}}
    animate ={{y: 0, opacity: 1}}
    layout 
    >
      {tasks.map((task) => (
        <Task onDelete={onDelete} important= {important} complete={complete} task={task} />
     ))} 
    </motion.div>
  );
}

export default Tasks;
