import {FaTimes, FaCheck} from 'react-icons/fa'


const Task = ({task, important, complete, onDelete}) => {
  return (
    <div onDoubleClick={() => important(task.id)} className={`task ${task.important ? 'important-task' : ''} ${task.completed ? 'completed-task' : ''} `} >
      <div className="icons">
        <FaTimes onClick={() => onDelete(task.id)} className="delete-btn" />
        <FaCheck onClick ={() => complete(task.id)} className="complete-btn" />
      </div>
      <h1> {task.text} </h1>
      <p> {task.day} at <span>{task.time}</span></p>
      <div className="container">
        {task.important && <h5 className="important-text text">Important Task! </h5>}
        {task.completed && <h5 className="completed-text text">Completed </h5>}
      </div>
    </div>
  );
}

export default Task;
